import React , { useState } from 'react';


export default function TextForm(props) {
    const handleUpClick=()=>{
        let newText = text.toUpperCase();
        setText(newText)
    }
    const handleDownClick=()=>{
        let newText = text.toLowerCase();
        setText(newText)
    }
    const handleClear=()=>{
        let newText = "";
        setText(newText)
    }
    const handleCopy=()=>{
        var text = document.getElementById("myBox");
        text.select();
        navigator.clipboard.writeText(text.value);
    } 
    const handleOnChange=(event)=>{
        setText(event.target.value);
    }
const [text, setText]= useState('');

  return (
      <>
    <div className='container' style={{ color: props.mode==='dark'?'white':'black'}}>
        <h1 className='my-4' >{props.heading}</h1>
  <div className="mb-3">
    {/* <label for="myBox" className="form-label">Example Textarea</label> */}
    <textarea  className="form-control" style={{ backgroundColor: props.mode==='dark'?'grey':'white', color: props.mode==='dark'?'white':'black'}} id="myBox" onChange={handleOnChange} rows="8" value={text}></textarea>
  </div>
  <button type="submit" className="btn btn-primary my-3" onClick={handleUpClick}>Convert to Uppercase</button>
  <button type="submit" className="btn btn-primary my-3 mx-3" onClick={handleDownClick}>Convert to Lowercase</button>
  <button type="submit" className="btn btn-primary my-3 mx-3" onClick={handleClear}>Clear text</button>
  <button type="submit" className="btn btn-primary my-3 mx-3" onClick={handleCopy}>Copy Text</button>
</div>
<div className="container" style={{ color: props.mode==='dark'?'white':'black'}}>
    <h1 className="my-3" >Your Text Summary</h1>
    <p>{text.split(" ").length} words and {text.length} characters</p>
    <p>{0.008 * text.split(" ").length} Minutes read</p>
    <h2>Preview</h2>
    <p>{text.length>0?text:"Enter text in above box to preview it here"}</p>
</div>
</>
  )
}
